<template>
  <div class="wsy_box">
    <!-- 顶部导航栏 -->
    <!-- <van-nav-bar @click="onClickLeft()" @click-right="onClickRight">
      <template #left>
        <van-icon name="arrow-left" @click="onClickLeft" />
      </template>
      <template #title>课程详情</template>
      <template #right>
        <van-icon name="share-o" />
      </template>
    </van-nav-bar>-->
    <div class="header_container">
      <div class="header_wrapper">
