      </van-popup>
      <!-- 分享弹出层 -->
    </div>
    <div class="wsy_bottom" v-show="wsy_Flag" @click="toStudy">立即学习</div>
    <div class="wsy_bottom" v-show="!wsy_Flag" @click="toPurchase">立即报名</div>
  </div>
</template>

<script>
import BetterScroll from "better-scroll";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      course_type: null,
      PopupIsShow: false,
      wsy_bs: null,
      gradeValue: 3,
      wsy_topFlag: false,
      wsy_top: ["课程介绍", "课程大纲", "课程评价"],
      active: 0,
      commentData: [
        {
          username: "1231321",
          gradeValue: 4,
          comment: "不错",
          time: "2020-09-02 21:03"
        },
        {
          username: "1231321",
          gradeValue: 4,
          comment: "不错",
          time: "2020-09-02 21:03"
        },
        {
          username: "1231321",
          gradeValue: 4,
          comment: "不错",
          time: "2020-09-02 21:03"
        },
        {
          username: "杨铮",
          gradeValue: 3,
          comment: "挺好的",
          time: "2020-09-02 21:03"
        },
        {
          username: "杨铮",
          gradeValue: 3,
          comment: "挺好的",
          time: "2020-09-02 21:03"
        },
        {
          username: "杨铮",
          gradeValue: 3,
          comment: "挺好的",
          time: "2020-09-02 21:03"
        },
        {
          username: "杨铮",
          gradeValue: 3,
          comment: "挺好的",
          time: "2020-09-02 21:03"
        },
        {
          username: "杨铮",
          gradeValue: 3,
          comment: "挺好的",
          time: "2020-09-02 21:03"
        },
        {
          username: "杨铮",
          gradeValue: 3,
          comment: "挺好的",
          time: "2020-09-02 21:03"
        },
        {
          username: "杨铮",
          gradeValue: 3,
          comment: "挺好的",
          time: "2020-09-02 21:03"
        }
      ],
      CourseInfo: {},
      wsy_Flag: null
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.wsy_bs = new BetterScroll(this.$refs.scrollBox, {
        probeType: 3,
        click: true
      });

      this.wsy_bs.on("scroll", pos => {
        if (pos.y <= -100) {
          this.wsy_topFlag = true;
        } else {
          this.wsy_topFlag = false;
        }
        if (pos.y <= -315 && pos.y > -440) {
          this.active = 0;
        } else if (pos.y <= -440 && pos.y > -550) {
          this.active = 1;
        } else if (pos.y <= -550) {
          this.active = 2;
        }
      });
    });
    this.CourseInfo =
      JSON.parse(window.localStorage.getItem("CourseInfo")) ||
      this.$route.params;
    this.wsy_Flag = this.CourseInfo.is_buy;
    this.course_type = this.CourseInfo.course_type;
    this.ajaxDetail();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    async ajaxDetail() {
      let { data } = await this.$http.get(
        `/api/app/courseInfo/basis_id=${this.CourseInfo.id}`
      );
      console.log(data.data.info);
      
      this.CourseInfo = data.data.info
    },
    //   分享
    onClickRight() {
      this.PopupIsShow = true;
    },
    //   返回首页
    onClickLeft() {
      this.$router.back(1);
    },
    // 跳转到提交订单页面
    toPurchase() {
      this.$router.push("/Purchase");
    },
    // 跳转到课程学习页面
    toStudy() {
      this.$router.push("/study-detail");
    },
    // 收藏
    course(num) {
      this.course_type = num;
      console.log(this.course_type);
    },
    // 点击滚动置顶高度
    wsy_scr(index) {
      this.active = index;
      if (index == 0) {
        this.wsy_bs.scrollTo(0, -315, 1000);
      } else if (index == 1) {
        this.wsy_bs.scrollTo(0, -440, 1000);
      } else if (index == 2) {
        this.wsy_bs.scrollTo(0, -550, 1000);
      }
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

